@import url(https://fonts.googleapis.com/css?family=Parisienne&display=swap);
@import url(https://fonts.googleapis.com/css?family=Parisienne&display=swap);
@import url(https://fonts.googleapis.com/css?family=Parisienne&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  min-height: 100vh;
}
:root{
  --farba-efektov: #ff7e5f;
}
nav{
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  background: rgb(49, 49, 49);
  font-size: 20px;
  color: white;
  box-shadow: 0px 0px 5px black;
}
::selection{
  background: black;
  color: white;
}
::-webkit-scrollbar{
  width: 5px;
}
::-webkit-scrollbar-thumb{
  box-shadow: inset 0px 0px 2px black;
  border-radius: 4px;
}
.fab{
  color: white;
  font-size: 25px;
  transform: .3s;
}
.fab:hover{
  color: #ff7e5f;
  color: var(--farba-efektov);
  transform: rotate(5deg);
}
.LOGO{
  position: relative;
  top: 0;
  left: 50px;
  font-size: 35px;
  font-weight: bold;
  text-shadow: 1px 1px 1px black;
  text-decoration: underline;
  transition: .3s;
  color: white;
  font-family: 'Parisienne', cursive;
}
.LOGO:hover{
  color: #ff7e5f;
  color: var(--farba-efektov);
}
.navbar{
  margin-left: auto;
  height: 100%;
  width: 30%;
  right: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  list-style: none;
}
.link{
  position: relative;
  text-decoration: none;
  color: white;
  text-transform: uppercase;
}
.link::before{
content: "";
position: absolute;
bottom: 0;
left: 50%;
height: 1px;
width: 0%;
background: #ff7e5f;
background: var(--farba-efektov);
transition: .3s;
}
.link::after{
  content: "";
  position: absolute;
  bottom: 0;
  right: 50%;
  height: 1px;
  width: 0%;
  background:#ff7e5f;
  background:var(--farba-efektov);
  transition: .3s;
  }
.link:hover::before{
  width: 50%;
}
.link:hover::after{
  width: 50%;
}
.body{
  position: relative;
  height: calc( 100% - 50px);
  top: 50px;
  width: 100%;
  background: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  overflow-x: hidden;
  color: black;
}
.ikonBox{
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  z-index: 2;
  left: 0%;
  top: 40%;
  height: 200px;
  width: 50px;
  background: rgb(49, 49, 49);
}
.icon{
  color: white;
  font-size: 25px;
}
.mobile{
  display: none;
}
.mobile-menu-btn{
  display: none;
}
@media screen and (max-width: 500px){
  .mobile{
    position: fixed;
    top: 50px;
    width: 100%;
    height: 100vh;
    z-index: 50;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: rgba(0,0,0,.95);
  }
  .link-mobile{
    color: white;
    text-decoration: none;
    font-weight: bold;
    margin: 30px 0px;
    font-size: 30px;
    text-shadow: 1px 1px 1px black;
    text-transform: uppercase;
  }
  .link{
    display: none;
  }
  .mobile-menu-btn{
    display: block;
    position: absolute;
    right: 20px;
    background: none;
    font-size: 40px;
    border: none;
    color: white;
    outline: none;
  }
}
.home{
    background: url(/Recepty/static/media/HomeWallpaper.c5df17fe.jpg) center;
    background-size: cover;
    height: calc(100vh - 50px);
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;
    text-align: center;
  }
  .overlay{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: rgba(0,0,0,.3);
    color: white;
  }
  .home-text{
    margin-bottom: 50px;
    font-size: 60px;
    font-family: 'Parisienne', cursive;
    text-shadow: 2px 2px 2px black;
    cursor: default;
  }
  .btn-More{
    position: relative;
    padding: 10px 50px;
    font-weight: 800;
    text-decoration: none;
    color: white;
    border: solid 4px white;
    text-transform: uppercase;
    font-size: 20px;
    background: rgba(0,0,0,.5);
    transition: .3s;
  }
  .btn-More span:nth-child(1){
    position: absolute;
    border-radius: 50%;
    height: 30px;
    width: 30px;
    background: white;
    top: -50px;
    right: 70px;
    display: none;
  }
  .btn-More span:nth-child(2){
    position: absolute;
    border-radius: 50%;
    height: 30px;
    width: 30px;
    background: white;
    top: -50px;
    left: 70px;
    display: none;
  }
  .btn-More:hover  span{
    display: block;
  }
  .btn-More span::before{
    content: "";
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%);
    height:10px;
    width: 10px;
    background: black;
    border-radius: 50%;
  }
  .btn-More:hover{
    border-radius: 0px 0px 50px 50px;
}

#next{
  min-height: calc(100vh - 200px);
  padding: 100px;
}
.card{
  height: auto;
  width: 300px;
  position: relative;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 0px 5px grey;
  transition: .3s;
}
.card:hover{
  box-shadow: 0px 0px 8px grey;
}
.card p{
  text-align: justify;
}
.card img{
  height: 250px;
  width: 100%;
}
.cardgroup{
  height: auto;
  width: 80vw;
  display: flex;
  justify-content: space-around;
}
@media screen and (max-width: 500px){
  .home-text{
    font-size: 50px;
    width: 80%;
  }
}
.wrapper{
    position: relative;
    min-height: calc(100vh - 90px);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    flex-direction: column;
}
.nadpis-strany{
    position: absolute;
    font-family: 'Parisienne', cursive;
    font-size: 80px;
    top: -35px;
    color: rgb(255, 255, 255);
    text-shadow: 0px 0px 4px rgb(46, 46, 46),
    3px 3px 3px rgb(46, 46, 46);
    cursor: default;
    transform: rotate(-5deg);
}
main{
    border-radius: 10px;
    height: 70vh;
    width: 80vw;
    background: #ff7e5f;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #feb47b, #ff7e5f); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    display: grid;
    grid-template-rows: 50px 1fr;
    grid-template-columns: 250px auto;
    padding: 10px;
    box-shadow: 5px 5px 5px grey;
}
aside{
position: relative;
grid-column: 1;
grid-row: 2;
overflow-y: auto;
overflow-x: hidden;
display: flex;
align-items: center;
flex-direction: column;
width: 100%;
box-shadow: inset 0px 0px 3px black;
border-radius: 5px 0px 0px 5px;
}
aside ul{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 200px;
}
aside li{
    position: relative;
    left: -20px;
    margin-bottom: 20px;
    padding: 1px 1px;
    border-radius: 20px;
    width: 225px;
    list-style: none;
    height: 40px;
    display: flex;
    align-items: center;
    background: #ff7e5f;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #feb47b, #ff7e5f); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    color: white;
    text-shadow: 2px 2px 2px black;
    box-shadow: 3px 3px 3px rgb(46, 46, 46),
                inset 0px 0px 2px black;
    cursor: pointer;
    transition: .2s;
}
aside li:hover{
    box-shadow: 5px 5px 5px rgb(46, 46, 46),
                inset 0px 0px 2px black;
    width: 230px;
}
.rec-img{
    height: 50px;
    width: 50px;
    border-radius: 50%;
    margin-right: 5px;
}
.zobraz{
    position: relative;
    height: 100%;
    width: 100%;
    background: white;
    box-shadow: inset 0px 0px 3px black;
    border-radius: 0px 5px 5px 0px;
}
.empty{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    font-family: 'Parisienne', cursive;
    font-size: 50px;
    text-shadow: 1px 1px 1px grey;
}
.receptBox{
    height: 100%;
    width: 100%;
    position: absolute;
    display: flex;
    justify-content: center;
    overflow: hidden;
}
.receptBox::after{
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, .6);
}
.receptBox h1{
    position: absolute;
    font-size: 40px;
    text-align: center;
    font-family: 'Parisienne', cursive;
    color: white;
    bottom: 10px;
    text-shadow: 0px 0px 5px black,
                 3px 3px 3px black;
    z-index: 3;
    width: 90%;
    cursor: default;
}
.img{
   height: 300px;
   width: 300px;
   border-radius: 50%;
   position: absolute;
   bottom: 100px;
   left: 50%;
   transform: translate(-50%);
   border: solid 10px #ff7e5f;
   z-index: 2;
   transition: .3s;
}
.img:hover{
    height: 310px;
    width: 310px;
    box-shadow: 0px 0px 5px black;
}
.bottom{
    position: absolute;
    bottom: -300px;
    left: -40px;
    height: 600px;
    width: 110%;
    background: #ff7e5f;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #feb47b, #ff7e5f); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    border-radius: 50%;
    z-index: 2;
}
.recept{
    position: relative;
    height: 100%;
    width: 100%;
}
.hladaj{
    grid-row: 1;
    grid-column: 2;
    display: flex;
    align-items: center;
    justify-content: center;
}
.hladaj input{
    outline: none;
    width: 400px;
    height: 28px;
    box-shadow: inset 0px 0px 3px rgb(0, 0, 0);
    padding-left: 10px;
    border-radius: 20px;
    border: none;
    transition: .1s;
}
.hladaj input:hover{
    box-shadow: inset 0px 0px 3px rgb(0, 0, 0),
    0px 0px 3px rgb(0, 0, 0);
}
.hladaj input:focus{
    box-shadow: inset 0px 0px 3px rgb(0, 0, 0),
    0px 0px 3px rgb(0, 0, 0);
}
.hladaj button{
    outline: none;
    width: 100px;
    height: 28px;
    border: none;
    background: #ff7e5f;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #feb47b, #ff7e5f); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    margin-left: 10px;
    border-radius: 20px;
    text-transform: uppercase;
    cursor: pointer;
    transition: .3s;
    font-weight: bold;
    color: white;
    text-shadow: 2px 2px 2px black;
    box-shadow: 0px 0px 1px black,
                inset 0px 0px 2px black;
}
.hladaj button:hover{
    box-shadow: 0px 0px 5px black;
}
.nadpis{
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Parisienne', cursive;
    font-size: 30px;
    text-shadow: 2px 2px 2px black;
    color: white;
    transform: rotate(-5deg);
    z-index: 2;
    cursor: default;
}
.close-aside{
    display: none;
}
@media screen and (max-width: 500px){
    .wrapper{
        min-height: 100vh;
        padding: 0;
        background: linear-gradient(to right, #feb47b, #ff7e5f);
    }
    .empty{
        font-size: 30px;
    }
    .nadpis{
        display: none;
    }
    .nadpis-strany{
        margin: 0;
        top: 10px;
        position: relative;
    }
    main{
        border-radius: 10px;
        height: 100%;
        width: 100%;
        display: grid;
        grid-template-rows: auto;
        grid-template-columns: 1fr;
        padding: 10px;
        box-shadow: none;
    }
    aside{
        top: 0;
        width: 100%;
        height: 100vh;
        left: -100%;
        position: absolute;
        z-index: 5;
        background: linear-gradient(to left, #feb47b, #ff7e5f);
        transition: 1s;
    }
    .show{
        left: 0;
    }
    .hidden{
        left: -100%;
    }
    aside button{
        position: absolute;
        z-index: 4;
        right: 0;
    }
    .hladaj{
        grid-column: 1;
    }
    .hladaj button{
        margin: 10px 0px;
    }
    .hladaj form{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }
    .hladaj form input{
        width: 80vw;
    }
    .receptBox{
        position: absolute;
    }
    .zobraz{
        grid-column: 1;
        grid-row: auto;
        width: 100vw;
        height: 500px;
    }
    .close-aside{
        position: absolute;
        right: 10px;
        top: 10px;
        border: none;
        background: transparent;
        color: white;
        font-size: 30px;
        outline: none;
        display: block;
    }
    .bottom{
        left: -40px;
        bottom: -350px;
        width: 120%;
    }
    .img{
        width: 250px;
        height: 250px;
    }
}
.contact{
    min-height: calc(100vh - 50px);
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: url(/Recepty/static/media/HomeWallpaper.c5df17fe.jpg) center;
    background-size: cover;
    position: relative;
    top: 50px;
}
.contact::before{
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, .3);
}
.contact-form{
    z-index: 2;
    height: 600px;
    width: 400px;
    background: rgba(0, 0, 0, .4);
    box-shadow: inset 0px 0px 4px black, 0px 0px 10px black;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    color: white;
    transition: .3s;
}
.contact-form:hover{
    background: rgba(0, 0, 0, .8);
}
.contact-form input, .contact-form textarea{
    font-family: sans-serif;
    padding: 5px;
    outline: none;
    border: none;
    background: rgba(255, 255, 255, .4);
    border-radius: 5px;
    color: white;
    box-shadow: inset 0px 0px 2px black, 0px 0px 5px black;
    width: 90%;
}
.contact-form textarea{
    height: 300px;
}
.contact-form button{
    cursor: pointer;
    width: 90%;
    border: none;
    box-shadow: inset 0px 0px 2px black, 0px 0px 5px black;
    height: 30px;
    text-transform: uppercase;
    background: rgba(255, 255, 255, .4);
    color: white;
    transition: .3s;
}
.contact-form button:hover{
    background: rgba(255, 255, 255, .8);
    color: black;
    font-weight: bold;
}
.contact-form label{
    font-family: 'Parisienne', cursive;
    font-size: 30px;
    text-shadow: 1px 1px 1px black;
}
::-webkit-input-placeholder{
    color: rgba(0, 0, 0, .5);
}
:-ms-input-placeholder{
    color: rgba(0, 0, 0, .5);
}
::-ms-input-placeholder{
    color: rgba(0, 0, 0, .5);
}
::placeholder{
    color: rgba(0, 0, 0, .5);
}
